<template>
  <div class="flex-center flex-column" id="main-conatiner">

    <img src="../assets/neres_poker_logo.png" width="350">

    <div id="input-conatiner">
      <div class="text-info">Nome do jogador</div>
      <input v-model="inputName" type="text" id="input-name" maxlength="20">
    </div>
    <button class="poker-button poker-button-primary" @click="onNewGame"> {{ isNewRoom ?  'Novo Jogo' : 'Entrar'}}</button>

  </div>
</template>

<script>
// @ is an alias to /src
import HelloWorld from '@/components/HelloWorld.vue'
import axios from 'axios';

export default {
  name: 'HomeView',

  data() {
      return {
        isNewRoom: true,
        inputName: "",
      }
  },

  components: {
    HelloWorld
  },

  created() {
    if(this.$route.params.idgame) {
        this.isNewRoom = false
      }
  },

  methods: {

    async onNewGame() {

      if(!this.inputName) 
        return

      var idgame = ""
      if(this.isNewRoom) {
        let result = await axios.get(`${window.location.origin}/api/newgame`)
        idgame = result["data"]["id"]
      } else {
        idgame = this.$route.params.idgame
      }

      sessionStorage.setItem(idgame, JSON.stringify({name: this.inputName}));
      this.$router.push(`/game/${idgame}`)
    }
  }
  
}
</script>

